.row {
  lost-utility: clearfix; }

.semaphore, .red-light, .yellow-light, .green-light {
  lost-column: 1/3 10px;
  text-align: center; }

.red-light {
  background-color: red;
  color: white; }

.yellow-light {
  background-color: yellow; }

.green-light {
  background-color: green;
  color: white; }
